import React from 'react';
import styled from 'styled-components';
import Swag1 from '../images/food/swag1.jpg';
import Brownies from '../images/food/brownies.jpg';
import Menu from '../images/menu-stock.png';

const TileWrapper = styled.div`
    margin: 60px 0px 35%;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    @media screen and (max-width: 540px){
        grid-template-columns: 1fr;
        row-gap: 80px;
        margin-bottom: 150px;
    }
`

const Tile = styled.div`
    height: 400px;
    width: max-content;
    > img{ 
        height: fill-available;
        width: 50vh;
    }
`

const FloatingName = styled.a`
    text-decoration: none;
    color: black;
    background-color: white;
    position: relative;
    width: 90%;
    height: 108px;
    top: 15%;
    left: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    @media screen and (max-width: 540px){
        width: 60%;
        margin: 0 auto;
      }
`
const Image = styled.img`
    width: fill-available;
`

export const FloatingTiles = () => {
    return(
        <TileWrapper>
            <Tile>
                <FloatingName href='/swag'>Swag</FloatingName>
                <Image src={Swag1} />
            </Tile>
            <Tile>
                <FloatingName href='/menu'>Menu</FloatingName>
                <Image src={Menu} />
            </Tile>
            <Tile>
                <FloatingName href='/menu'>Order</FloatingName>
                <Image src={Brownies} />
            </Tile>
        </TileWrapper>
    )
}